<template>
    <div class="card" v-bind:class="design?'text-white bg-info':'text-white bg-dark'">
          <div class="card-header text-center">
            {{datefun()}}
          </div>
          <div class="card-body my-1" v-bind:class="design?day:night">
            <p class="card-title text-center location">{{location}}, {{country}}</p>
            <div class="row mb-1 text-center ">
              <div class="col">
               Longitude: {{longitude}}
              </div>
              <div class="col">
              Latitude {{latitude}}
              </div>
            </div>
            <div class="card-text text-center">
              <p class="temp pt-2">{{tempe}}°C</p>
            </div>
            <p class="text-center">Feels like {{feels_like}}°C</p>
            <p class="text-center">{{possibile}}: {{description}}</p>
            <div class="text-center my-1">
            <img  v-bind:src="icons" alt="weather-img"/>
            </div>
            <div class="row text-center my-1">
              <div class="col">
                Max: {{high}}°C
              </div>
              <div class="col">
                Min: {{low}}°C
              </div>
            </div>
          </div></div>
</template>
<script>
export default{
    name:'SideBar',
    props:['design','day','night','location','country','longitude','latitude','datefun',
  'tempe','feels_like','possible','description','icons','high','low']
}
</script>
