<template>
  <div id="app">
    <!--Navbar-->
    <NavBar :name="navTitle" :design="isDay"/>
    
    <!--Sidebar start-->
    <div class="row mx-0 px-0">
      <div class="col-lg-4" v-bind:class="isDay?'day1':'night'">
        <h1 class="text-center mt-5">Weather in</h1>
        <form v-on:submit.prevent="getWeather">
          <div class="mb-3">
            <input type="text" class="form-control" placeholder="Enter City..." v-model="citySearch" />
          </div>
        </form>
        <div v-if="weather.location!=''">
          <SideBar :design="isDay" :day="day" :night="night" :location="weather.location"
          :country="weather.country" :longitude="weather.longitude" :latitude="weather.latitude"
          :datefun="dateBuilder" :tempe="weather.temp"  :feels_like="weather.feels" :possible="weather.possibility"
          :description="weather.desc" :icons="weather.image" :high="weather.high" :low="weather.low"/>
        </div>
      </div>
      <!--Sidebar end-->
      <!--Middle part-->
      <div class="col-lg-8">
        <div v-if="weather.location!=''">
          <!--Hourly temp variation: fetching data from api
          <div class="row" >
         
             <table> 
              <tr>
                <th>Date and time</th>
                <th>Temp</th>
              </tr>
              <tr v-for="(a,index) in dates" :key="index">
                <span v-if="index<=8">
                <td>{{a.dt_txt}}--</td>
                  <td>{{a.main.temp}}</td>
                  </span>
              </tr>
            </table> 
          </div>-->
          <div class="row">
            <LineChart/>
            </div>

          <!--Weather Highlights-->
          <div class="row">
            <!--UV index-->
            <div class="col-lg-4">
              <Highlights :design="isDay" :property="weather.uvi" name="UV Index"/>
            </div>
            <!--Air visibility-->  
            <div class="col-lg-4">
              <Highlights :design="isDay" :property="weather.visible" name="Visibility"/>
            </div>
            <!--Wind Status-->
            <div class="col-lg-4">
              <Wind :design="isDay" :property="weather.speed" :property1="weather.degree" name="Wind Speed" name1="Wind Degree"/>
            </div>
          </div>
        </div>
      </div>
    </div>
 
  </div>
</template>

<script>
import NavBar from './components/NavBar.vue';
import Highlights from './components/Highlights.vue';
import Wind from './components/Wind.vue';
import SideBar from './components/SideBar.vue';
import LineChart from './components/LineChart.vue';

export default {
  name: 'App',
  components:{
    NavBar,
    Highlights,
    Wind,
    SideBar,
    LineChart
  },
  data(){
    return{
      navTitle:"Weather Dashboard",
      url_base:"http://api.openweathermap.org/data/2.5/",
      api_key:"91eb954317c8e810793e9844b6415822",
      citySearch:"",
      isDay:true,
      weather:
      {
        location:"",
        country:"",
        longitude:"",
        latitude:"",
        temp:"",
        feels:"",
        possibility:"",
        desc:"",
        image:"",
        high:"",
        low:"",
        icon:"",
        speed:"",
        degree:"",
        visible:"",
        uvi:"",
        dates:[],   //line added
        temperature:[],//line added
      }
    };
  },
  methods:{
    getWeather:async function(){
      console.log(this.citySearch);
      const url=`${this.url_base}weather?q=${this.citySearch}&appid=${this.api_key}&units=metric`;
      const response=await fetch(url);
      const data=await response.json();
      console.log(data);
      //lines added before this.citySearch=""
      const url3=`${this.url_base}forecast?q=${this.citySearch}&appid=${this.api_key}&units=metric`;
      const res2=await fetch(url3);
      const ans2=await res2.json();
      console.log(ans2);
      this.dates=ans2.list;
         
      this.citySearch="";
      this.weather.location=data.name;
      this.weather.country=data.sys.country;
      this.weather.longitude=data.coord.lon;
      this.weather.latitude=data.coord.lat;
      this.weather.temp=Math.round(data.main.temp);
      this.weather.feels=Math.round(data.main.feels_like);
      this.weather.possibility=data.weather[0].main;
      this.weather.desc=data.weather[0].description;
      this.weather.high=Math.round(data.main.temp_max);
      this.weather.low=Math.round(data.main.temp_min);
      const time=data.weather[0].icon;
      this.weather.speed=data.wind.speed;
      this.weather.degree=data.wind.deg;
      this.weather.visible=data.visibility;
      this.weather.image="http://openweathermap.org/img/wn/"+data.weather[0].icon+"@2x.png";
      if(time.includes("n")){
        this.isDay=false;
      }
      else{
        this.isDay=true;
      }
      const url2=`https://api.openweathermap.org/data/2.5/onecall?lat=${this.weather.latitude}&lon=${this.weather.longitude}&exclude=minutely,daily,alerts&units=metric&appid=${this.api_key}`;
      const res=await fetch(url2);
      const ans=await res.json();
      console.log(ans);
      this.weather.uvi=ans.current.uvi;
      
    },
    dateBuilder(){
      let d=new Date();
      let months=["January","February","March","April","May","June","July","August","September",
     "October","November","December"];
     let days=["Sunday","Monday","Tuesday","Wednesday","Thursday","Friday","Saturday"];
     let day=days[d.getDay()];
     let date=d.getDate();
     let month=months[d.getMonth()];
     let year=d.getFullYear();
     return `${day}, ${date} ${month}, ${year}`;
    }
  },
}
</script>

<style>
@import './assets/custom.css'; 

