 <script>
import { Line } from "vue3-chart-v2";
export default {
  extends: Line,
  data() {
    return {
      chartData: {
        labels: [
          "12:00 am",
          "4:00 am",
          "8:00 am",
          "12:00 pm",
          "4:00 pm",
          "8:00 pm",
        ],
        datasets: [
          {
            label: "Line",
            data: [20, 24, 28, 30, 35, 16],
            fill: false,
            tension: 0.1,
            borderColor: "blue",
            backgroundColor: "blue",
            borderWidth: 2,
          },
        ],
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        title: {
          display: true,
          text: "Temperature Variation",
          fontColor: "black",
          fontSize: 24,
          position: "left",
          padding: 16,
        },
        legend: {
          align: "end",
          labels: {
            fontColor: "black",
            fontSize: 14,
          },
        },
        layout: {
          padding: 16,
        },
        scales: {
          yAxes: [
            {
              ticks: {
                beginAtZero: true,
              },
              gridLines: {
                display: true,
              },
            },
          ],
          xAxes: [
            {
              gridLines: {
                display: false,
              },
            },
          ],
        },
      },
    };
  },
  mounted() {
    this.renderChart(this.chartData, this.options);
  },
};
</script>

